/*------------------------------------------------------------
[3. LAYOUT]
------------------------------------------------------------*/

.page-container {
    padding: 20px;
    position: relative;
    display: block;
}


/* Page Header */

.page-header {
    background: #fff;
    border-radius: 15px;
    box-shadow: 0px 1px 3px 0px rgba(54, 74, 99, 0.05);
    position: fixed;
    width: calc(100% - 40px);
    z-index: 999;
    border: 1px solid #E5E9F2;
}

.page-header::before {
    content: '';
    display: block;
    width: 100%;
    height: 105px;
    background: -webkit-gradient(linear, left top, left bottom, color-stop(44%, hsla(0, 0%, 97.3%, .94)), color-stop(73%, hsla(0, 0%, 97.3%, .49)), to(hsla(0, 0%, 100%, 0)));
    position: fixed;
    top: 0;
    left: 0;
}

.page-header .navbar {
    background: #fff;
    border-radius: 15px;
    padding: 15px 25px;
    display: flex!important;
}

.page-header .navbar .logo {
    display: flex!important;
    flex: 2;
}

.page-header .navbar #navbarNav #leftNav>li>a {
    color: #5b5b5b;
    font-size: 14px;
    -webkit-transition: all .2s ease-in-out;
    -moz-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    border-radius: 10px;
    padding: 9px 10px;
    line-height: 22px;
}

.page-header .navbar #navbarNav #leftNav>li>a:hover {
    background: #F3F6FA;
    color: #7889FF;
}

.page-header .navbar #navbarNav #leftNav>li>a i {
    line-height: 22px;
    vertical-align: middle;
    font-size: 22px;
}

.page-header .navbar #navbarNav #leftNav>li>a#sidebar-toggle {
    padding: 9px 0;
    margin-right: 10px;
    transform: rotate(0deg);
    -webkit-transition: all .2s ease-in-out;
    -moz-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

body.sidebar-hidden .page-header .navbar #navbarNav #leftNav>li>a#sidebar-toggle {
    transform: rotate(-180deg);
}

.page-header .navbar #navbarNav #leftNav>li>a#sidebar-toggle:hover {
    background: transparent;
}

.page-header .navbar #headerNav,
.page-header .navbar #navbarNav {
    display: flex!important;
    flex: 10
}

.page-header .navbar #headerNav .navbar-nav,
.page-header .navbar #navbarNav .navbar-nav {
    flex-direction: row!important;
}

.page-header #sidebar-toggle svg {
    width: 22px;
    height: 22px;
}

.page-header .navbar #headerNav>ul>li>a {
    padding: 9px 0;
    margin: 0 10px;
    color: #5b5b5b;
    -webkit-transition: all .2s ease-in-out;
    -moz-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

.page-header .navbar #headerNav>ul>li>a svg {
    line-height: 22px;
    vertical-align: middle;
    width: 21px;
    height: 21px;
}

.page-header .navbar #headerNav>ul>li>a:hover {
    color: #7889FF;
}

.page-header .navbar #headerNav>ul>li>a.notifications-dropdown {
    padding: 9px 15px;
    background: #fad7dd;
    color: #EE6E83;
    line-height: 22px;
    border-radius: 40px;
    min-width: 40px;
    text-align: center;
    font-weight: bold;
}

.page-header .navbar #headerNav>ul>li>a.profile-dropdown {
    padding: 0;
    margin-right: 0;
}

.page-header .navbar #headerNav>ul>li>a img {
    width: 40px;
}

.page-header .navbar .navbar-nav {
    flex: 10;
    padding: 0;
}

.page-header .navbar .navbar-brand {
    margin-right: 0;
    background: url('../images/logo@2x.png') center center no-repeat;
    background-size: cover;
    width: 40px;
    height: 40px;
    margin: 0 auto;
}

.page-header #headerNav {
    flex: 10;
    align-items: flex-end;
    flex-direction: column;
}

.page-header .header-notif {
    padding: 13px 26px;
    -webkit-transition: all .1s ease-in-out;
    -moz-transition: all .1s ease-in-out;
    -o-transition: all .1s ease-in-out;
    transition: all .1s ease-in-out;
}

.page-header .header-notif:hover {
    background-color: #F3F6F9;
}

.page-header .header-notif .notif-image {
    float: left;
    margin-right: 10px;
    margin-top: 3px;
    border-radius: 50%;
}

.page-header .header-notif .notif-image span {
    width: 40px;
    height: 40px;
    display: block;
    line-height: 40px;
    text-align: center;
    border-radius: 100%;
    display: inline-block;
}

.page-header .header-notif .notif-image img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

.page-header .header-notif .notif-text {
    width: 223px;
    display: block;
    padding: 0 5px;
    display: inline-block;
    color: #5b5b5b;
    font-weight: 400;
}

.page-header .header-notif .notif-text .bold-notif-text {
    font-weight: 500;
}

.page-header .header-notif .notif-text p {
    margin: 0;
}

.page-header .header-notif .notif-text small {
    opacity: 0.7;
}

.profile-drop-menu a span {
    margin-left: 25px;
}

.search-drop-menu {
    width: 340px;
}

.notif-drop-menu {
    width: 340px;
    max-height: 420px;
    overflow-y: auto;
}

.notif-drop-menu .dropdown-header {
    margin-bottom: 5px;
}

.profile-drop-menu a svg {
    margin-right: 10px;
    width: 19px;
    height: 19px;
}

.navbar-nav .dropdown-menu {
    position: absolute!important;
    visibility: hidden;
    opacity: 0;
    margin-top: 30px;
    display: block;
    -webkit-transition: all .2s ease-in-out;
    -moz-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

.navbar-nav .dropdown-menu.show {
    margin-top: 10px;
    opacity: 1;
    visibility: visible;
}


/* SlimScroll */

.slimScrollBar {
    background: rgba(0, 0, 0, .2)!important;
    border-radius: 4px!important;
}


/* Page Sidebar */

.page-sidebar {
    position: fixed;
    height: calc(100% - 148px);
    background: #fff;
    box-shadow: 0px 1px 3px 0px rgba(54, 74, 99, 0.05);
    top: 118px;
    border-radius: 15px;
    width: 260px;
    -webkit-transition: all .2s ease-in-out;
    -moz-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    transform: scale(1, 1);
    transform-origin: left;
    overflow: visible;
}

.page-sidebar .ps__rail-y {
    margin-top: 8px!important;
    margin-bottom: 8px!important;
    background-color: transparent!important;
}

.page-sidebar .accordion-menu {
    height: 100%!important;
    padding: 20px 15px;
}

.page-sidebar .accordion-menu>li>a {
    display: block;
    color: #5b5b5b;
    -webkit-transition: all .1s ease-in-out;
    -moz-transition: all .1s ease-in-out;
    -o-transition: all .1s ease-in-out;
    transition: all .1s ease-in-out;
    line-height: 45px;
    padding: 0 15px;
    text-decoration: none;
}

.page-sidebar .accordion-menu>li.active-page>a {
    border-radius: 10px;
    background-color: #F3F6F9;
    color: #7888FC;
    font-weight: 500;
}

.page-sidebar .accordion-menu>li.active-page>a>svg {
    color: #7888FC!important;
}

.page-sidebar .accordion-menu>li.active-page ul li a.active {
    font-weight: 500;
    color: #3d3d3d;
}

.page-sidebar .accordion-menu>li>a:hover svg {
    margin-left: 5px;
}

.page-sidebar .accordion-menu>li>a>svg {
    width: 21px;
    height: 21px;
    line-height: 40px;
    text-align: center;
    vertical-align: text-top;
    color: #5b5b5b;
    margin-right: 15px;
    -webkit-transition: all .2s ease-in-out;
    -moz-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

.page-sidebar .accordion-menu li.sidebar-title {
    font-weight: 500;
    padding: 10px 15px;
    font-size: .875rem;
    color: #6c757d;
    opacity: 0.8;
}

.page-sidebar .accordion-menu li a .dropdown-icon {
    float: right;
    vertical-align: middle;
    line-height: 44px;
    font-size: 10px;
    -webkit-transition: all .2s ease-in-out;
    -moz-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

.page-sidebar .accordion-menu li.open>a>.dropdown-icon {
    visibility: visible;
    transform: rotate(90deg)
}

.page-sidebar .accordion-menu li ul {
    padding: 5px 0;
    list-style: none;
}

.page-sidebar .accordion-menu li ul li a {
    color: #5b5b5b;
    display: block;
    padding: 10px 15px;
    font-size: 14px;
    position: relative;
    -webkit-transition: all .15s ease-in-out;
    -moz-transition: all .15s ease-in-out;
    -o-transition: all .15s ease-in-out;
    transition: all .15s ease-in-out;
    text-decoration: none;
}

.page-sidebar .accordion-menu li ul li a:hover {
    margin-left: 5px;
}

.page-sidebar .accordion-menu li ul li a i {
    font-size: 10px;
    padding-right: 21px;
    padding-left: 6px;
}

body.sidebar-hidden .page-sidebar {
    transform: scale(0, 1);
}


/*------------------------------------------------------------
[4. PAGE CONTENT]
------------------------------------------------------------*/

.page-content {
    margin-top: 71px;
    margin-left: 260px;
    -webkit-transition: all .2s ease-in-out;
    -moz-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

.main-wrapper {
    padding: 28px 0 28px 28px;
    -webkit-transition: all .2s ease-in-out;
    -moz-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

body.sidebar-hidden .page-content {
    margin-left: 0;
}

body.sidebar-hidden .main-wrapper {
    padding-left: 0;
}


/*------------------------------------------------------------
[5. UI ELEMENTS]
------------------------------------------------------------*/


/* Popovers */

.popover {
    margin: 20px;
    border: 0;
    border-radius: 10px;
    box-shadow: 0 0 1.25rem rgba(31, 45, 61, .08);
    color: #5b5b5b;
    font-family: 'Poppins', sans-serif!important;
}

.popover-header {
    padding: 15px 25px 0;
    background: transparent;
    border-bottom-color: transparent;
    font-size: 14px;
    opacity: .7;
}

.popover-body {
    padding: 15px 25px;
    font-size: 13px;
    color: #7D7D83;
}

.popover .arrow:before {
    display: none;
}

.bs-popover-auto[data-popper-placement^=right]>.popover-arrow::before,
.bs-popover-end>.popover-arrow::before,
.bs-popover-auto[data-popper-placement^=left]>.popover-arrow::before,
.bs-popover-start>.popover-arrow::before,
.bs-popover-auto[data-popper-placement^=top]>.popover-arrow::before,
.bs-popover-top>.popover-arrow::before,
.bs-popover-auto[data-popper-placement^=bottom]>.popover-arrow::before,
.bs-popover-bottom>.popover-arrow::before {
    display: none;
}


/* Row */

.row>* {
    padding-right: 14px;
    padding-left: 14px;
}


/* Cards */

.card {
    margin-bottom: 28px;
    border-radius: 15px;
    border: none;
    box-shadow: 0px 1px 3px 0px rgba(54, 74, 99, 0.05);
}

.card-title {
    font-size: 18px;
    color: #5b5b5b;
    margin-bottom: 30px;
}

.card-body {
    padding: 30px;
}

.card-description {
    margin-bottom: 20px;
}

.card-img,
.card-img-top {
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
}

.card-img,
.card-img-bottom {
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
