    color: #ffaf0f;
}

.tr-card-bg-info {
    background-color: #efe8ff!important;
    color: #9C6EFC;
}


/* Loader */

.loader {
    position: fixed;
    height: 100%;
    width: 100%;
    background: #fff;
    z-index: 9999;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    -webkit-transition: all .2s ease-in-out;
    -moz-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

.loader .spinner-grow {
    top: 50%;
    left: 50%;
    margin: -20px 0 0 -20px;
    position: absolute;
    width: 40px;
    height: 40px;
}

.no-loader .loader {
    visibility: hidden;
    opacity: 0;
}


/*------------------------------------------------------------
[9. RESPONSIVE LAYOUT]
------------------------------------------------------------*/

@media (max-width: 1350px) {
    .page-header .navbar #navbarNav {
        flex: 0;
        margin-right: 15px;
    }
    .page-header .navbar .logo {
        flex: 0!important;
    }
    .page-header .navbar #navbarNav .navbar-nav li a:not(#sidebar-toggle) {
        display: none;
    }
    .page-header .navbar #navbarNav #leftNav>li>a#sidebar-toggle {
        transform: rotate(-180deg);
    }
    body.sidebar-hidden .page-header .navbar #navbarNav #leftNav>li>a#sidebar-toggle {
        transform: rotate(0deg);
    }
    .page-sidebar {
        transform: scale(0, 1);
        z-index: 999;
    }
    body.sidebar-hidden .page-sidebar {
        transform: scale(1, 1);
    }
    .page-content {
        margin-left: 0;
    }
    .page-content .main-wrapper {
        padding-left: 0;
    }
    body.sidebar-hidden .page-content {
        transform: translateX(260px);
    }
    body.sidebar-hidden .page-content {
        padding-left: 20px;
    }
}

@media (max-width: 1200px) {
    .profile-header {
        margin-top: -60px;
    }
    .profile-cover {
        height: 200px;
    }
    .profile-img {
        display: block;
        float: none!important;
    }
    .profile-img img {
        margin: 0 auto;
        display: inherit;
        width: 100px!important;
        height: 100px!important;
        border-radius: 100px!important;
    }
    .profile-name {
        display: block;
        float: none!important;
        text-align: center;
        margin: 0!important;
    }
    .profile-name h3 {
        color: #333!important;
    }
    .profile-header .profile-header-menu {
        display: block;
        float: none;
        margin: 0;
        text-align: center;
    }
    .profile-header .profile-header-menu ul li {
        display: block;
        padding: 0;
        width: calc(100%/6);
        text-align: center;
    }
    .profile-header .profile-header-menu ul li a {
        padding: 10px;
        text-align: center;
        height: auto!important;
    }
    .profile-header .profile-header-menu ul li a:hover,
    .profile-header .profile-header-menu ul li a.active {
        color: #000;
    }
    .mail-actions {
        display: none;
    }
    .mail-title {
        max-width: 100%;
    }
}

@media (max-width: 992px) {
    .error-image {
        display: none;
    }
    .error-info {
        width: 100%;
        text-align: center;
    }
    .error-info h1 {
        font-size: 79px;
        color: #fefefe;
    }
}